#services {
	width: 100%;
	margin: 150px 0;

	&::after {
		content: '';
		display: block;
		clear: both;
	}

	.inner {
		width: 1180px;
		margin: 0 auto;

		h1 {
			text-align: left;
			margin-bottom: 15px;
			position: relative;
			font: 500 30px/1 'Montserrat';
			color: #666;

			&::after {
				content: '';
				display: block;
				width: 240px;
				height: 5px;
				transition: 0.5s;
				position: absolute;
				bottom: 2px;
				z-index: -1;
			}
		}

		.wrap {
			width: 100%;

			&::after {
				content: '';
				display: block;
				clear: both;
			}

			article {
				width: 300px;
				float: left;
				margin-right: 130px;
				text-align: center;

				&:nth-of-type(1) {
					text-align: left;

					p {
						text-align: left;
						margin-bottom: 20px;
					}
				}

				&:last-child {
					margin-right: 0;
				}

				> svg {
					transition: 0.5s;
					font-size: 30px;
					margin-bottom: 20px;
				}

				h2 {
					font: 500 14px/1.2 'Montserrat';
					color: #555;
					margin-bottom: 10px;
				}

				p {
					font: 300 12px/1.5 'Montserrat';
					color: #555;
				}

				.btn {
					display: block;
					width: 170px;
					height: 45px;

					text-align: center;
					font: 500 12px/45px 'Roboto';
					color: #fff;
					box-shadow: 2px 5px 15px #eee;
					transition: 0.5s;
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	#services {
		margin: 100px 0;

		.inner {
			width: 100%;
			margin: 0 auto;
			padding: 0 30px;

			h1 {
				text-align: center;

				&::after {
					left: 50%;
					transform: translateX(-50%);
				}
			}

			.wrap {
				article {
					float: none;
					margin: 30px auto;
					text-align: center;

					&:nth-of-type(1) {
						text-align: center;

						p {
							text-align: center;
						}
					}

					&:last-child {
						margin: 0 auto;
					}

					.btn {
						display: none;
					}
				}
			}
		}
	}
}
