header {
	width: 100%;
	height: 120px;

	.inner {
		width: 100%;
		height: 100%;
		position: relative;
		transition: 0.5s;
		overflow: hidden;

		h1 {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 12%;
			height: 100%;
			background: #fff;
			float: left;

			a {
				svg {
					transition: 0.5s;
					font-size: 80px;
					line-height: 120px;
				}
			}
		}

		#gnb {
			float: left;
			width: 88%;
			height: 100%;
			display: flex;
			align-items: center;

			li {
				a {
					color: #fff;
					font: 500 14px/1 'Montserrat';
					padding: 0 45px;
					position: relative;

					&:hover::after {
						content: '';
						display: block;
						width: 50%;
						height: 2px;
						background-color: rgba(255, 255, 255, 0.5);
						position: absolute;
						bottom: -5px;
						left: 50%;
						transform: translateX(-50%);
					}
				}
			}
		}

		.color {
			position: absolute;
			right: 50px;
			top: 50%;
			transform: translateY(-50%);
			text-indent: 99999px;
			display: flex;

			> div {
				width: 20px;
				height: 20px;
				border: 0.5px solid #fff;
				box-sizing: border-box;
				cursor: pointer;
				margin-left: 20px;

				&:nth-of-type(1) {
					background: #ee7d31;
				}
				&:nth-of-type(2) {
					background: #18b4e7;
				}
				&:nth-of-type(3) {
					background: #00df70;
				}
			}
		}

		.fa-bars {
			cursor: pointer;
			position: absolute;
			right: 50px;
			top: 50%;
			transform: translateY(-50%);
			color: #fff;
			display: none;
		}
	}
}

.sub {
	.inner {
		background: $orange;

		h1 {
			a {
				svg {
					color: $orange;
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	header {
		height: 100px;

		.inner {
			h1 {
				width: 100%;
				transition: 0.5s;

				a {
					svg {
						color: #fff !important;
						line-height: 100px;
					}
				}
			}

			#gnb {
				display: none;
			}

			.color {
				right: 0;
				left: 30px;

				> div {
					width: 15px;
					height: 15px;
					margin-left: 15px;
				}
			}

			.fa-bars {
				display: block;
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	header {
		height: 80px;

		.inner {
			h1 {
				a {
					svg {
						font-size: 65px;
						line-height: 80px;
					}
				}
			}

			.color {
				right: 0;
				left: 10px;

				> div {
					width: 12px;
					height: 12px;
					margin-left: 10px;
				}
			}

			.fa-bars {
				right: 20px;
			}
		}
	}
}
